<template>
    <div class="slot-machine-container">
        <div class="slot-viewport" :style="{
            height: candidateBoxHeight + 'px',
            width: candidateBoxWidth + 'px'
        }">
            <div class="slot-reel" :style="reelStyle" ref="reel">
                <CandidateBox v-for="(candidate, index) in reelCandidates" :key="candidate.sn + '-' + index"
                    :candidate-info="candidate" :candidate-index="index"></CandidateBox>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
// 由於發現 string.randArray 不存在，我們移除對 'lib/common/util' 中 string 的依賴
import CandidateBox from 'components/CandidateBox/main.vue';

// 滾動配置
const ROLL_DURATION_MS = 4000; // 滾動總時長 (4 秒)
const BASE_ROLLS = 3; // 至少滾動的圈數

export default {
    components: { CandidateBox },

    data() {
        return {
            reelCandidates: [], // 滾動軸上的候選人列表
            isRolling: false,
            scrollDistance: 0, // 單位：px, 滾動的距離 (負值代表向上滾動)
            transitionDuration: 0, // 單位：ms, CSS 動畫時長
            RANDOM_FILL_LENGTH: 30, // 初始滾動列表的隨機長度
        };
    },
    computed: {
        ...mapGetters([
            'candidateList', 
            'candidateMapping', 
            'config',
            'focusCandidateSN', 
            'haveAwardCandidateSN',
        ]),

        // 從 config 獲取 CandidateBox 的尺寸 (包含 Margin)
        candidateBoxHeight() {
            // 確保 config 中 boxMV 是垂直 Margin
            return this.config.boxHeight + (this.config.boxMV || 0) * 2;
        },
        candidateBoxWidth() {
            // 確保 config 中 boxMH 是水平 Margin
            return this.config.boxWidth + (this.config.boxMH || 0) * 2;
        },
        reelStyle() {
            return {
                transform: `translateY(${this.scrollDistance}px)`,
                // 設定緩動曲線，讓動畫看起來更像物理慣性
                transition: `transform ${this.transitionDuration}ms cubic-bezier(0.25, 0.46, 0.45, 0.94)`,
            };
        },
        // 獲取所有未中獎的 SN 列表
        availableSNList() {
            return this.candidateList
                .filter(c => !c.del && !this.haveAwardCandidateSN.includes(c.sn))
                .map(c => c.sn);
        }
    },
    watch: {
        // 監聽焦點 SN 狀態，如果被設定且目前不在滾動，則開始滾動
        focusCandidateSN(newSNs) {
            if (Array.isArray(newSNs) && newSNs.length > 0 && !this.isRolling) {
                const winnerSN = newSNs[0];
                this.startRolling(winnerSN);
            }
        }
    },
    mounted() {
        this.initReel(); // 首次載入時初始化滾動條內容
    },
    methods: {
        ...mapMutations(['triggerModal']),

        /**
         * 【修正】實現在陣列中隨機選取一個元素的函式 (替代 string.randArray)
         * @param {Array} arr 要隨機選取的陣列
         * @returns {any} 陣列中的隨機元素 (或 null)
         */
        randArray(arr) {
            if (!arr || arr.length === 0) return null;
            const randomIndex = Math.floor(Math.random() * arr.length);
            return arr[randomIndex];
        },

        /**
         * 初始化滾動條內容：填充隨機的候選人
         */
        initReel() {
            const candidates = [];

            if (this.availableSNList.length === 0) {
                this.reelCandidates = [];
                return;
            }

            // 填充隨機候選人，模擬滾動效果
            for (let i = 0; i < this.RANDOM_FILL_LENGTH; i++) {
                // 【修正點】：使用 this.randArray
                const randomSN = this.randArray(this.availableSNList);
                if (randomSN) {
                    candidates.push(this.candidateMapping[randomSN]);
                }
            }
            this.reelCandidates = candidates;
        },

        /**
         * 啟動滾動動畫
         * @param {string} winnerSN 最終中獎者的 SN
         */
        startRolling(winnerSN) {
            this.isRolling = true;
            this.transitionDuration = 0;
            this.scrollDistance = 0; // 重置位置

            // 1. 初始化滾動條 (填充隨機候選人)
            this.initReel();
            const randomPartLength = this.reelCandidates.length;

            // 2. 將中獎者插入到列表末端
            const winnerInfo = this.candidateMapping[winnerSN];
            // 插入中獎者三次，確保停靠時中獎者在視野中心 (停在倒數第二個)
            this.reelCandidates.push(winnerInfo, winnerInfo, winnerInfo);

            // 3. 計算最終停止位置
            const boxSize = this.candidateBoxHeight;
            const totalReelLength = this.reelCandidates.length; 

            // 中獎者在列表中的索引 (倒數第二個)
            const winnerIndex = totalReelLength - 2; 

            // 總共要滾動的 box 數量 = (隨機部分長度 * 至少滾動圈數) + 中獎者的索引
            const finalStopIndex = (randomPartLength * BASE_ROLLS) + winnerIndex;
            const finalDistance = finalStopIndex * boxSize;


            // 設置滾動時間和開始動畫
            this.transitionDuration = ROLL_DURATION_MS;

            this.$nextTick(() => {
                // 將滾軸向上移動 finalDistance (所以是負值)
                this.scrollDistance = -finalDistance;
            });

            // 4. 監聽滾動結束
            setTimeout(() => {
                this.stopRolling(winnerSN);
            }, this.transitionDuration);
        },

        /**
         * 停止滾動動畫並清理狀態 (使用 requestAnimationFrame 確保下一幀重置)
         * @param {string} winnerSN 最終中獎者的 SN
         */
        stopRolling(winnerSN) {
            this.isRolling = false;
            
            // 步驟 A: 移除 CSS 轉場效果 (立即停止在 finalDistance)
            this.transitionDuration = 0;

            // 使用 requestAnimationFrame 確保在下一次瀏覽器繪製前完成瞬時重置，避免閃爍
            window.requestAnimationFrame(() => {
                // 步驟 B: 清理滾動條內容，只保留中獎者
                const winnerInfo = this.candidateMapping[winnerSN];
                this.reelCandidates = [winnerInfo];

                // 步驟 C: 重置滾動軸位置
                this.scrollDistance = 0; 

                // 使用 $nextTick 確保 DOM 更新後再觸發 Modal
                this.$nextTick(() => {
                    this.triggerModal({ key: 'Lucky' });
                });
            });
        },

    },
};
</script>

